import "../styles/Navigation.css";
import Main from './AboutMe';
import React from 'react';
import { useHistory } from "react-router-dom";
import { Redirect } from "react-router-dom"



function Navigation() {
  const history = useHistory();

  function handleContactClick() {
    <Redirect to="/react-portfolio/contact"/>
  }

  function handleProjectClick() {
    history.push("/project");
  }

  function handleResumeClick() {
    history.push("/react-portfolio/resume");
  }

 

  return (
    <nav>
      <div className="row">
        <div className="col">
          <a type="button"
        href="/react-portfolio">About Me</a>
        </div>
        <div className="col">
          <a type="button" href ="/project" onClick={handleProjectClick} >Projects</a>
        </div>
        <div className="col">
          <a type="button" href="/contact" onClick ={handleContactClick}>Contact</a>
        </div>
        <div className="col">
          <a type="button" href ="/resume" onClick={handleResumeClick}>Resume</a>
        </div>
      </div>
    </nav>
  );
}
export default Navigation;
